:root {
  --fz-main: 12px;
  --lh-main: 16px; }

@media (min-width: 768px) {
  :root {
    --fz-main: 16px;
    --lh-main: 24px; } }

@media (min-width: 1440px) {
  :root {
    --fz-menu: 20px;
    --lh-menu: 30px; } }

:root {
  --cl-bg-main: rgb(255, 255, 255);
  --cl-bg-relate: rgb(230, 230, 230);
  --cl-bg-contrast: rgb(0, 0, 0);
  --cl-bg-burger: rgb(140, 140, 140);
  --cl-bg-menu: rgb(255, 255, 255);
  --cl-txt-main: rgb(68, 68, 68);
  --cl-bg-button: rgb(52, 168, 219);
  --cl-bg-button-hover: rgba(52, 168, 219, 0.3);
  --cl-bg-button-active: rgba(52, 168, 219, 0.3);
  --cl-txt-button: rgb(255, 255, 255);
  --cl-txt-button-active: rgba(255, 255, 255, 0.3);
  --cl-shd-footer: rgba(255, 255, 255,0.15);
  --cl-fill-footer-icon: rgb(102, 102, 102);
  --cl-fill-footer-icon-hover: rgb(104, 183, 56);
  --cl-bg-decor: rgb(253, 216, 60); }

.list {
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  list-style: none; }

.link {
  box-sizing: border-box;
  display: block;
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden; }
